<script>
import axios from "axios";
export default {
  name: "Header",
  props: {},
  methods: {
    himzo() {
        console.log("valami")
        this.$router.push('/')
    },
    aboutus() {
        console.log("valami")
        this.$router.push('/aboutus')
    },
    profileroute() {
        console.log("valami")
        this.$router.push('/profile')
    },
    signin() {
        console.log("valami")
        this.$router.push('/signin')
    },
    userorder() {
        console.log("valami")
        this.$router.push('/userorder')
    },
    allorder() {
        console.log("valami")
        this.$router.push('/allorder')
    }
  },
  data() {
    return {
      titleHimzo: "Hmz",
      order: "Rendels",
      myOrders: "Rendelseim",
      aboutUs: "Rlunk",
      username: "",
      profile: "Profilom",
      signOut: "Kilps",
      signIn: "Bejelentkezs",
      allOrder: "Rendelsek",
      role: null
    };
  },
  async mounted() {
    //Username
    try {
      const res = await axios.get("http://localhost:52140/api/User");
      this.username = res.data.userName;
      this.role = res.data.role;
    } catch (e) {
      console.log(e);
    }
  }
};
</script>

